.button {
    border-radius: 5px;
    border: 2px solid #479ff8;
    background-color: #fff;
    color: #479ff8;
    text-align: center;
    font-size: 18px;
    padding:6px;
    width: 215px;
    transition: all 0.5s;
    cursor: pointer;
    margin-left: 0%;
    margin-top: 1%;
}
  
  .button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .button:hover span {
    padding-right: 25px;
  }
  
  .button:hover span:after {
    opacity: 1;
    right: 0;
  }
  @media (min-width: 1000px) {
    .container{
        max-width: 1200px;
    }
}

